import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import { Container } from '@components/global';
import Call2Act from '@ui/Call2Act';

const Header = () => (
  <StaticQuery
    query={graphql`
      query {
        art_build: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "build_hide" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `}
    render={data => (
      <HeaderWrapper>
        <Container>
          <Grid>
            <Text>
              <h1>
              Our site is launching soon.
              </h1>
              {/*<h1>
              Introducing Hai
              </h1>
              <br/>
              <p>
              The innovative universal earphone for hearing aid users, cochlear implant users, and normal hearing people.<br/> 
              </p>
              <Call2Act text="Find out more" href="https://rearia.io/product_hai/"/>
              <br />*/}
            </Text>
            <Art>
              <Img fluid={data.art_build.childImageSharp.fluid} />
            </Art> 
          </Grid>
        </Container>
      </HeaderWrapper>
    )}
  />
);

const HeaderWrapper = styled.header`
  background-color: ${props => props.theme.color.background.S100};
  padding-top: 80px;

  @media (max-width: ${props => props.theme.screen.md}) {
    padding-top: 128px;
  }
`;

const Art = styled.figure`
  width: 105%;
  margin-left: 0px;
  margin-bottom: 0vh;

  @media (max-width: ${props => props.theme.screen.md}) {
    width: 100%;
  }  

  > div {
    width: 100%;
    margin-bottom: -3%;

    @media (max-width: ${props => props.theme.screen.md}) {
      width: 100%;
      margin-top: -50px;
    }
  }
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: 2fr 3fr;
  align-items: center;
  grid-gap: 64px;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    grid-gap: 80px;

    > ${Art} {
      order: 2;
    }
  }
`;

const Text = styled.div`
  justify-self: start;
  font-weight: black;
  @media (max-width: ${props => props.theme.screen.md}) {
    justify-self: start;
  }
`;


export default Header;
